import {Modal, Button, WingBlank, WhiteSpace, Toast} from 'antd-mobile';
var fetch = require('node-fetch');
const prompt = Modal.prompt;

export const Login = React.createClass({
    contextTypes: {
        router: React.PropTypes.object
    },
    getInitialState() {
        return {
            windowWidth: (typeof window !== 'undefined') ? window.innerWidth : undefined,
            windowHeight: (typeof window !== 'undefined') ? window.innerHeight : undefined,
        };
    },
    handleResize(e) {
        this.setState({
            windowWidth: window.innerWidth,
            windowHeight: window.innerHeight
        });
    },
    componentDidMount() {
        window.addEventListener('resize', this.handleResize);
    },
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    },
    getLotteryRecordListInfo(){
        let that = this;
        let Test_Query = `query  getLotteryRecordListFunc($offset: Int!, $count: Int!) {
                             getLotteryRecordList(offset:$offset,count:$count) {
                                  code
                                  type
                                  content
                                }
                             }`;
        fetch(globalServerIP, {
            method: 'POST',
            body: JSON.stringify(
                {
                    "query": Test_Query,
                    "variables": {
                        "offset": arguments[0],
                        "count": 11
                    }
                }
            ),
            headers: {'Content-Type': 'application/json'}
        })
            .then(function (res) {
                return res.json();
            }).then(function (json) {
            let data = JSON.parse(json.data.getLotteryRecordList.content);
            // totalCount,gainNumSum, lotteryRecordList, pageInfo
            //设置变量
            that.setState({
                totalCount: data.totalCount,
                gainNumSum: data.gainNumSum,
                lotteryRecordList: data.lotteryRecordList,
                paginationBtnDisable: false
            });
        }).catch(err=> {
            console.log(err);
        });
    },
    render() {
        const width = this.state.windowWidth;
        const height = this.state.windowHeight;
        return (
            <div>
                <WingBlank size="lg">
                    <WhiteSpace size="lg"/>
                    <div style={{display:'flex',alignItems: 'center',justifyContent: 'center'}}>
                        <h1>后台管理系统</h1>
                    </div>
                    <WhiteSpace size="lg"/>
                    <Button
                        type="primary"
                        onClick={() => prompt(
                          '登录',
                          '输入用户名和密码',
                          function(login, password) {
                            if (login==='manager'&&password==='123456'){
                                const location = {
                                    pathname: '/menu',
                                    state: { fromDashboard: true }
                                };
                                this.context.router.history.push(location);
                            }else {
                                Toast.fail('用户名或者密码错误!!!', 1);
                            }
                          }.bind(this),
                          'login-password',
                        )}
                    >管理员登录</Button>

                    <WhiteSpace size="lg"/>
                </WingBlank>
            </div>);
    }
});


