import {NavBar, Pagination, Button, Modal, Toast} from 'antd-mobile';
var fetch = require('node-fetch');

const alert = Modal.alert;

export const PlayerPointAddAndSubtractApproval = React.createClass({
    contextTypes: {
        router: React.PropTypes.object
    },
    getInitialState() {
        return {
            windowWidth: (typeof window !== 'undefined') ? window.innerWidth : undefined,
            windowHeight: (typeof window !== 'undefined') ? window.innerHeight : undefined,
            totalCount: 10,
            pointAddAndSubtractRecordList: [],
            paginationBtnDisable: false,
            currentPage:0
        };
    },
    handleResize(e) {
        this.setState({
            windowWidth: window.innerWidth,
            windowHeight: window.innerHeight
        });
    },
    componentDidMount() {
        window.addEventListener('resize', this.handleResize);
        this.getPointAddAndSubtractRecordListInfo(0);
    },
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    },
    getPointAddAndSubtractRecordListInfo(){
        let that = this;
        let Test_Query = `query  getPointAddAndSubtractRecordListFunc($offset: Int!, $count: Int!) {
                             getPointAddAndSubtractRecordList(offset:$offset,count:$count) {
                                  code
                                  type
                                  content
                                }
                             }`;
        fetch(globalServerIP, {
            method: 'POST',
            body: JSON.stringify(
                {
                    "query": Test_Query,
                    "variables": {
                        "offset": arguments[0],
                        "count": 11
                    }
                }
            ),
            headers: {'Content-Type': 'application/json'}
        })
            .then(function (res) {
                return res.json();
            }).then(function (json) {
            let data = JSON.parse(json.data.getPointAddAndSubtractRecordList.content);
            // totalCount,gainNumSum, lotteryRecordList, pageInfo
            //设置变量
            that.setState({
                totalCount: data.totalCount,
                pointAddAndSubtractRecordList: data.pointAddAndSubtractRecordList,
                paginationBtnDisable: false
            });
        }).catch(err=> {
            console.log(err);
        });
    },
    render() {
        const width = this.state.windowWidth;
        const height = this.state.windowHeight;
        let jj = 1;
        return (
            <div>
                <NavBar leftContent="返回菜单"
                        mode="light"
                        onLeftClick={() => {
                            const location = {
                            pathname: '/menu',
                            state: { fromDashboard: true }
                        };

                        this.context.router.history.push(location);
                        }}
                >玩家上下分审批</NavBar>
                <div style={{display:'flex',flexDirection: 'column'}}>
                    <div
                        style={{display:'flex',alignItems: 'center',justifyContent: 'center',backgroundColor: '#00BFFF',marginLeft:10,marginRight:10,height:height*0.06,paddingLeft:20,borderRadius:height*0.01,marginTop:5}}>
                        <div style={{marginRight:10,color:'white',flex:2}}>类型</div>
                        <div style={{marginRight:10,color:'white',flex:3}}>账号</div>
                        <div style={{marginRight:10,color:'white',flex:3}}>昵称</div>
                        <div style={{marginRight:10,color:'white',flex:3}}>金额</div>
                        <div style={{marginRight:10,color:'white',flex:3}}>驳回</div>
                        <div style={{marginRight:10,color:'white',flex:3}}>通过</div>
                    </div>
                    {this.state.pointAddAndSubtractRecordList.map((ii) => (
                        <div key={jj++}
                             style={{display:'flex',alignItems: 'center',justifyContent: 'center',backgroundColor: 'white',marginTop:5,marginLeft:10,marginRight:10,height:height*0.06,paddingLeft:20,borderRadius:height*0.01}}>
                            <div
                                style={{marginRight:10,flex:2,color:ii.additionPoints>0?'rgba(255,0,0,0.6)':'rgba(0,128,0,0.6)'}}>{ii.additionPoints > 0 ? '上分' : '下分'}</div>
                            <div style={{marginRight:10,color:'black',flex:3}}>{ii.user.accountName}</div>
                            <div style={{marginRight:10,color:'black',flex:3}}>{ii.user.nickName}</div>
                            <div style={{marginRight:10,color:'black',flex:3}}>{ii.additionPoints}</div>
                            <div style={{marginRight:10,flex:3}}>
                                <Button
                                    style={{backgroundColor:'rgba(255,0,0,0.6)',color:"white"}} size="small"
                                    onClick={function() {
                                    let that = this;
                                            alert('驳回', '确定驳回用户 "'+ii.user.accountName +'" 金额 '+ii.additionPoints +' 的请求么?',
                                            [
                                              { text: '取消' },
                                              {
                                                text: '驳回',
                                                onPress: value => new Promise((resolve) => {
                                                    Toast.loading('操作中...', 0);
                                                        let Test_Query = `query  updateApprovalFlagByIDFunc($additionPoints: Int!,$approvalFlag: String!,$ID: String!) {
                                                                        updateApprovalFlagByID(additionPoints:$additionPoints,approvalFlag:$approvalFlag,ID:$ID) {
                                                                             code
                                                                             type
                                                                             content
                                                                           }
                                                                        }`;
                                                        fetch(globalServerIP, {
                                                            method: 'POST',
                                                            body: JSON.stringify(
                                                                {
                                                                    "query": Test_Query,
                                                                    "variables": {
                                                                        "additionPoints": ii.additionPoints,
                                                                        "ID": ii._id,
                                                                        "approvalFlag": '驳回'
                                                                    }
                                                                }
                                                            ),
                                                            headers: {'Content-Type': 'application/json'}
                                                        })
                                                            .then(function (res) {
                                                                return res.json();
                                                            }).then(function (json) {
                                                                if(json.data.updateApprovalFlagByID.content === '审批驳回'){
                                                                    resolve();
                                                                    return setTimeout(() => {
                                                                      that.getPointAddAndSubtractRecordListInfo(that.state.currentPage);
                                                                      Toast.info('审批驳回', 0.7);
                                                                    }, 700);
                                                                }else {
                                                                    Toast.info(json.data.updateApprovalFlagByID.content, 0.7);
                                                                }
                                                        }).catch((err)=> {
                                                            console.log(err);
                                                            Toast.fail('网络错误', 0.7);
                                                        });
                                                }).catch(err=>{console.log(err)})
                                              }
                                            ]);
                                }.bind(this,ii)}>驳回</Button>
                            </div>
                            <div style={{marginRight:10,flex:3}}>
                                <Button style={{backgroundColor:'rgba(0,128,0,0.6)',color:"white"}} size="small"
                                        onClick={function() {
                                        let that = this;
                                            alert('通过', '确定通过用户 "'+ii.user.accountName +'" 金额 '+ii.additionPoints +' 的请求么?',
                                            [
                                              { text: '取消' },
                                              {
                                                text: '通过',
                                                onPress: value => new Promise((resolve) => {
                                                    Toast.loading('操作中...', 0);
                                                        let Test_Query = `query  updateApprovalFlagByIDFunc($additionPoints: Int!,$approvalFlag: String!,$ID: String!) {
                                                                        updateApprovalFlagByID(additionPoints:$additionPoints,approvalFlag:$approvalFlag,ID:$ID) {
                                                                             code
                                                                             type
                                                                             content
                                                                           }
                                                                        }`;
                                                        fetch(globalServerIP, {
                                                            method: 'POST',
                                                            body: JSON.stringify(
                                                                {
                                                                    "query": Test_Query,
                                                                    "variables": {
                                                                        "additionPoints": ii.additionPoints,
                                                                        "ID": ii._id,
                                                                        "approvalFlag": '通过'
                                                                    }
                                                                }
                                                            ),
                                                            headers: {'Content-Type': 'application/json'}
                                                        })
                                                            .then(function (res) {
                                                                return res.json();
                                                            }).then(function (json) {
                                                                if(json.data.updateApprovalFlagByID.content === '审批通过'){
                                                                    resolve();
                                                                    return setTimeout(() => {
                                                                      that.getPointAddAndSubtractRecordListInfo(that.state.currentPage);
                                                                      Toast.info('审批通过', 0.7);
                                                                    }, 700);
                                                                }else {
                                                                    Toast.info(json.data.updateApprovalFlagByID.content, 0.7);
                                                                }
                                                        }).catch((err)=> {
                                                            console.log(err);
                                                            Toast.fail('网络错误', 0.7);
                                                        });
                                                }).catch(err=>{console.log(err)})
                                              }
                                            ]);
                                        }.bind(this,ii)}>通过</Button>
                            </div>
                        </div>
                    ))}
                </div>


                <Pagination
                    disabled={this.state.paginationBtnDisable}
                    onChange={(num)=>{
                        this.setState({
                            currentPage:num,
                            paginationBtnDisable:true
                        });
                        this.getPointAddAndSubtractRecordListInfo(num*11);
                    }}
                    style={{margin:10 }} total={Math.ceil(this.state.totalCount/11)}
                    current={this.state.currentPage} locale={ {prevText: 'Prev',nextText: 'Next'}}
                />
            </div>);
    }
});


