import React from 'react';
import ReactDOM from 'react-dom';
import {serverIP} from './global.jsx';
import {Menu} from './components/Menu.jsx';
import {Login} from './components/Login.jsx';
import {BankerAccount} from './components/BankerAccount.jsx';
import {PlayerBettingDetails} from './components/PlayerBettingDetails.jsx';
import {PlayerPointAddAndSubtract} from './components/PlayerPointAddAndSubtract.jsx';
import {PlayerPointAddAndSubtractApproval} from './components/PlayerPointAddAndSubtractApproval.jsx';
import './index.less';
import {
    BrowserRouter as Router,
    Route,
    Link
} from 'react-router-dom'

ReactDOM.render((
    <Router>
        <div className="container" id="container">
            <div className="body">
                <Route exact path="/" component={Login}/>
                <Route exact path="/bankerAccount" component={BankerAccount}/>
                <Route exact path="/menu" component={Menu}/>
                <Route path="/playerBettingDetails" component={PlayerBettingDetails}/>
                <Route path="/playerPointAddAndSubtract" component={PlayerPointAddAndSubtract}/>
                <Route path="/playerPointAddAndSubtractApproval" component={PlayerPointAddAndSubtractApproval}/>
            </div>
        </div>
    </Router>
), document.getElementById('root'));


