import {List} from 'antd-mobile';

const Item = List.Item;
const Brief = Item.Brief;

export const Menu = React.createClass({
    contextTypes: {
        router: React.PropTypes.object
    },

    render() {
        return (
            <div>
                <List renderHeader={() => '功能菜单'} style={{flexBasis: "initial"}}>
                    <Item
                        arrow="horizontal"
                        multipleLine
                        onClick={() => {
                            const location = {
                                pathname: '/bankerAccount',
                                state: { fromDashboard: true }
                            };
                            setTimeout(function() {
                                this.context.router.history.push(location);
                            }.bind(this) , 500);
                        }}
                        platform="android"
                    >
                        庄家赢亏统计<Brief>庄家赢亏统计Brief</Brief>
                    </Item>
                    <Item
                        arrow="horizontal"
                        multipleLine
                        onClick={() => {
                            const location = {
                                pathname: '/playerBettingDetails',
                                state: { fromDashboard: true }
                            };
                            setTimeout(function() {
                                this.context.router.history.push(location);
                            }.bind(this) , 500);
                        }}
                        platform="android"
                    >
                        玩家下注明细<Brief>玩家下注明细Brief</Brief>
                    </Item>
                    <Item
                        arrow="horizontal"
                        multipleLine
                        onClick={() => {
                            const location = {
                                pathname: '/playerPointAddAndSubtract',
                                state: { fromDashboard: true }
                            };
                            setTimeout(function() {
                                this.context.router.history.push(location);
                            }.bind(this) , 500);
                        }}
                        platform="android"
                    >
                        玩家上下分<Brief>玩家上下分Brief</Brief>
                    </Item>
                    <Item
                        arrow="horizontal"
                        multipleLine
                        onClick={() => {
                            const location = {
                                pathname: '/playerPointAddAndSubtractApproval',
                                state: { fromDashboard: true }
                            };
                            setTimeout(function() {
                                this.context.router.history.push(location);
                            }.bind(this) , 500);
                        }}
                        platform="android"
                    >
                        玩家上下分审批<Brief>超级管理员才可审批Brief</Brief>
                    </Item>
                </List>
            </div>);
    }
});


