import {NavBar, Pagination, Toast} from 'antd-mobile';

export const PlayerBettingDetails = React.createClass({
    contextTypes: {
        router: React.PropTypes.object
    },
    getInitialState() {
        return {
            windowWidth: (typeof window !== 'undefined') ? window.innerWidth : undefined,
            windowHeight: (typeof window !== 'undefined') ? window.innerHeight : undefined,
            gainNumSum: "loading",
            totalCount: 10,
            bettingRecordList: [],
            paginationBtnDisable: false,
            currentPage:0
        };
    },
    handleResize(e) {
        this.setState({
            windowWidth: window.innerWidth,
            windowHeight: window.innerHeight
        });
    },
    componentDidMount() {
        window.addEventListener('resize', this.handleResize);
        this.getLotteryRecordListInfo(0);
    },
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    },
    getLotteryRecordListInfo(){
        let that = this;
        let Test_Query = `query  getBettingRecordListFunc($offset: Int!, $count: Int!) {
                             getBettingRecordList(offset:$offset,count:$count) {
                                  code
                                  type
                                  content
                                }
                             }`;
        fetch(globalServerIP, {
            method: 'POST',
            body: JSON.stringify(
                {
                    "query": Test_Query,
                    "variables": {
                        "offset": arguments[0],
                        "count": 5
                    }
                }
            ),
            headers: {'Content-Type': 'application/json'}
        })
            .then(function (res) {
                return res.json();
            }).then(function (json) {
            let data = JSON.parse(json.data.getBettingRecordList.content);
            // totalCount,gainNumSum, lotteryRecordList, pageInfo
            for (let i0 = 0; i0 < data.bettingRecordList.length; i0++) {
                let bettingInfomation = '';
                let bettingSum = 0;
                let bettingGainSum = 0;
                for (let i1 = 0; i1 < data.bettingRecordList[i0].bettingContents.length; i1++) {
                    let bettingName = '';
                    if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "big") {
                        bettingName = "大";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "small") {
                        bettingName = "小";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "single") {
                        bettingName = "单";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "double") {
                        bettingName = "双";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "bigSingle") {
                        bettingName = "大单";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "smallSingle") {
                        bettingName = "小单";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "bigDouble") {
                        bettingName = "大双";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "smallDouble") {
                        bettingName = "小双";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "maximum") {
                        bettingName = "极大";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "minimal") {
                        bettingName = "极小";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "leopard") {
                        bettingName = "豹子";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "sequence") {
                        bettingName = "顺子";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "pair") {
                        bettingName = "对子";
                    } else if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "point") {
                        bettingName = "点数字";
                    } else {
                        bettingName = "奇怪" + data.bettingRecordList[i0].bettingContents[i1].bettingName;
                    }
                    data.bettingRecordList[i0].bettingContents[i1].bettingName = bettingName;
                    bettingSum = bettingSum + data.bettingRecordList[i0].bettingContents[i1].bettingNum;

                    if (data.bettingRecordList[i0].bettingContents[i1].bettingName === "点数字") {
                        bettingInfomation = bettingInfomation + data.bettingRecordList[i0].bettingContents[i1].bettingName + data.bettingRecordList[i0].bettingContents[i1].pointNum + ' ' + data.bettingRecordList[i0].bettingContents[i1].bettingNum + ','
                    } else {
                        bettingInfomation = bettingInfomation + data.bettingRecordList[i0].bettingContents[i1].bettingName + ' ' + data.bettingRecordList[i0].bettingContents[i1].bettingNum + ','
                    }
                }

                for (let i1 = 0; i1 < data.bettingRecordList[i0].bettingGain.length; i1++) {
                    bettingGainSum = bettingGainSum + data.bettingRecordList[i0].bettingGain[i1].bettingNum * data.bettingRecordList[i0].bettingGain[i1].oddsNum;
                }

                data.bettingRecordList[i0].bettingInfomation = bettingInfomation;
                data.bettingRecordList[i0].bettingSum = bettingSum;
                data.bettingRecordList[i0].bettingGainSum = bettingGainSum;
            }

            //设置变量
            that.setState({
                totalCount: data.totalCount,
                bettingRecordList: data.bettingRecordList,
                paginationBtnDisable: false
            });
        }).catch(err=> {
            console.log(err);
        });
    },
    render() {
        const width = this.state.windowWidth;
        const height = this.state.windowHeight;
        let jj = 1;
        return (
            <div>
                <NavBar leftContent="返回菜单"
                        mode="light"
                        onLeftClick={() => {
                            const location = {
                            pathname: '/menu',
                            state: { fromDashboard: true }
                        };

                        this.context.router.history.push(location);
                        }}
                >玩家下注明细</NavBar>
                <div style={{display:'flex',flexDirection: 'column'}}>
                    <div
                        style={{display:'flex',alignItems: 'center',justifyContent: 'center',backgroundColor: '#00BFFF',marginLeft:10,marginRight:10,height:height*0.06,paddingLeft:20,borderRadius:height*0.01,marginTop:5}}>
                        <div style={{marginRight:10,color:'white',flex:2}}>期号</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>账号</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>时间</div>
                        <div style={{marginRight:10,color:'white',flex:4}}>下注信息</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>总额</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>盈亏</div>
                    </div>
                    {this.state.bettingRecordList.map((ii) => (
                        <div key={jj++}
                             style={{display:'flex',alignItems: 'center',justifyContent: 'center',backgroundColor: 'white',marginTop:5,marginLeft:10,marginRight:10,height:height*0.15,paddingLeft:20,borderRadius:height*0.01}}>
                            <div style={{marginRight:10,color:'black',flex:2}}>{ii.periodNum}</div>
                            <div style={{marginRight:10,color:'black',flex:2}}>{ii.accountName}</div>
                            <div style={{marginRight:10,color:'black',flex:2}}>{ii.bettingDate}</div>
                            <div style={{marginRight:10,color:'black',flex:4}}>{ii.bettingInfomation}</div>
                            <div style={{marginRight:10,color:'black',flex:2}}>{ii.bettingSum}</div>
                            <div style={{marginRight:10,color:function(iiTemp) {
                                                                            if(!iiTemp.settleFlag){
                                                                                return 'rgba(255,165,0,0.8)';
                                                                            }else if (iiTemp.settleFlag && (iiTemp.bettingSum - iiTemp.bettingGainSum >= 0 )){
                                                                                return'rgba(0,128,0,0.6)';
                                                                            }else if (iiTemp.settleFlag && (iiTemp.bettingSum - iiTemp.bettingGainSum < 0 )){
                                                                                return 'rgba(255,0,0,0.6)';
                                                                            }else {
                                                                                return 'black';
                                                                            }

                            }(ii),flex:2}}>
                                {ii.settleFlag?ii.bettingGainSum-ii.bettingSum:"未开奖"}
                            </div>
                        </div>
                    ))}
                </div>


                <Pagination
                    disabled={this.state.paginationBtnDisable}
                    onChange={(num)=>{
                        this.setState({
                            currentPage:num,
                            paginationBtnDisable:true
                        });
                        this.getLotteryRecordListInfo(num*5);
                    }}
                    style={{margin:10 }} total={Math.ceil(this.state.totalCount/5)}
                    current={this.state.currentPage} locale={ {prevText: 'Prev',nextText: 'Next'}}
                />
            </div>);
    }
});


