import {NavBar, Pagination, Button, Modal, Toast} from 'antd-mobile';
var fetch = require('node-fetch');

const prompt = Modal.prompt;

export const PlayerPointAddAndSubtract = React.createClass({
    contextTypes: {
        router: React.PropTypes.object
    },
    getInitialState() {
        return {
            windowWidth: (typeof window !== 'undefined') ? window.innerWidth : undefined,
            windowHeight: (typeof window !== 'undefined') ? window.innerHeight : undefined,
            totalCount: 10,
            userList: [],
            paginationBtnDisable: false,
            currentPage:0
        };
    },
    handleResize(e) {
        this.setState({
            windowWidth: window.innerWidth,
            windowHeight: window.innerHeight
        });
    },
    componentDidMount() {
        window.addEventListener('resize', this.handleResize);
        this.getUserListInfo(0);
    },
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    },
    getUserListInfo(){
        let that = this;
        let Test_Query = `query  getUserListFunc($offset: Int!, $count: Int!) {
                             getUserList(offset:$offset,count:$count) {
                                  code
                                  type
                                  content
                                }
                             }`;
        fetch(globalServerIP, {
            method: 'POST',
            body: JSON.stringify(
                {
                    "query": Test_Query,
                    "variables": {
                        "offset": arguments[0],
                        "count": 11
                    }
                }
            ),
            headers: {'Content-Type': 'application/json'}
        })
            .then(function (res) {
                return res.json();
            }).then(function (json) {
            let data = JSON.parse(json.data.getUserList.content);
            // totalCount,gainNumSum, lotteryRecordList, pageInfo
            //设置变量
            that.setState({
                totalCount: data.totalCount,
                userList: data.userList,
                paginationBtnDisable: false
            });
        }).catch(err=> {
            console.log(err);
        });
    },
    render() {
        const width = this.state.windowWidth;
        const height = this.state.windowHeight;
        let jj = 1;
        return (
            <div>
                <NavBar leftContent="返回菜单"
                        mode="light"
                        onLeftClick={() => {
                            const location = {
                            pathname: '/menu',
                            state: { fromDashboard: true }
                        };

                        this.context.router.history.push(location);
                        }}
                >玩家上下分</NavBar>
                <div style={{display:'flex',flexDirection: 'column'}}>
                    <div
                        style={{display:'flex',alignItems: 'center',justifyContent: 'center',backgroundColor: '#00BFFF',marginLeft:10,marginRight:10,height:height*0.06,paddingLeft:20,borderRadius:height*0.01,marginTop:5}}>
                        <div style={{marginRight:10,color:'white',flex:2}}>账号</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>昵称</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>电话</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>QQ</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>金额</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>上分</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>下分</div>
                    </div>
                    {this.state.userList.map((ii) => (
                        <div key={jj++}
                             style={{display:'flex',alignItems: 'center',justifyContent: 'center',backgroundColor: 'white',marginTop:5,marginLeft:10,marginRight:10,height:height*0.06,paddingLeft:20,borderRadius:height*0.01}}>
                            <div style={{marginRight:10,color:'black',flex:2}}>{ii.accountName}</div>
                            <div style={{marginRight:10,color:'black',flex:2}}>{ii.nickName}</div>
                            <div style={{marginRight:10,color:'black',flex:2}}>{ii.phone}</div>
                            <div style={{marginRight:10,color:'black',flex:2}}>{ii.qqNumber}</div>
                            <div style={{marginRight:10,color:'black',flex:2}}>{ii.goldPoints}</div>
                            <div style={{marginRight:10,color:'black',flex:2}}>
                                <Button style={{backgroundColor:'rgba(255,0,0,0.6)',color:"white"}} size="small"
                                        onClick={function() {
                                            prompt('上分', '确定为用户 "'+ii.accountName +'" 上分:',
                                            [
                                              { text: '取消' },
                                              {
                                                text: '提交审批',
                                                onPress: value => new Promise((resolve) => {
                                                    Toast.loading('操作中...', 0);
                                                    if(Number(value).toString() !== 'NaN' && Number(value) >0){
                                                        let Test_Query = `query  insertPointAddAndSubtractRecordFunc($additionPoints: Int!,$user_fid: String!) {
                                                                        insertPointAddAndSubtractRecord(additionPoints:$additionPoints,user_fid:$user_fid) {
                                                                             code
                                                                             type
                                                                             content
                                                                           }
                                                                        }`;
                                                        fetch(globalServerIP, {
                                                            method: 'POST',
                                                            body: JSON.stringify(
                                                                {
                                                                    "query": Test_Query,
                                                                    "variables": {
                                                                        "additionPoints": Number(value),
                                                                        "user_fid": ii._id
                                                                    }
                                                                }
                                                            ),
                                                            headers: {'Content-Type': 'application/json'}
                                                        })
                                                            .then(function (res) {
                                                                return res.json();
                                                            }).then(function (json) {
                                                                if(json.data.insertPointAddAndSubtractRecord.content === '提交审批成功'){
                                                                    resolve();
                                                                    return setTimeout(() => {
                                                                      Toast.info('已提交审批', 0.7);
                                                                    }, 700);
                                                                }else {
                                                                    Toast.info(json.data.insertPointAddAndSubtractRecord.content, 0.7);
                                                                }
                                                        }).catch((err)=> {
                                                            console.log(err);
                                                            Toast.fail('网络错误', 0.7);
                                                        });
                                                    }else {
                                                        Toast.fail('请输入正确金额', 0.7);
                                                    }
                                                }).catch(err=>{console.log(err)})
                                              }
                                            ]);
                                        }.bind(ii)}>上分</Button></div>
                            <div style={{marginRight:10,color:'black',flex:2}}><Button
                                style={{backgroundColor:'rgba(0,128,0,0.6)',color:"white"}} size="small"
                                onClick={function() {
                                            prompt('下分', '确定为用户 "'+ii.accountName +'" 下分:',
                                            [
                                              { text: '取消' },
                                              {
                                                text: '提交审批',
                                                onPress: value => new Promise((resolve) => {
                                                    Toast.loading('操作中...', 0);
                                                    if(Number(value).toString() !== 'NaN' && Number(value) >0){
                                                        let Test_Query = `query  insertPointAddAndSubtractRecordFunc($additionPoints: Int!,$user_fid: String!) {
                                                                        insertPointAddAndSubtractRecord(additionPoints:$additionPoints,user_fid:$user_fid) {
                                                                             code
                                                                             type
                                                                             content
                                                                           }
                                                                        }`;
                                                        fetch(globalServerIP, {
                                                            method: 'POST',
                                                            body: JSON.stringify(
                                                                {
                                                                    "query": Test_Query,
                                                                    "variables": {
                                                                        "additionPoints": 0-Number(value),
                                                                        "user_fid": ii._id
                                                                    }
                                                                }
                                                            ),
                                                            headers: {'Content-Type': 'application/json'}
                                                        })
                                                            .then(function (res) {
                                                                return res.json();
                                                            }).then(function (json) {
                                                                if(json.data.insertPointAddAndSubtractRecord.content === '提交审批成功'){
                                                                    resolve();
                                                                    return setTimeout(() => {
                                                                      Toast.info('已提交审批', 0.7);
                                                                    }, 700);
                                                                }else {
                                                                    Toast.info(json.data.insertPointAddAndSubtractRecord.content, 0.7);
                                                                }
                                                        }).catch((err)=> {
                                                            console.log(err);
                                                            Toast.fail('网络错误', 0.7);
                                                        });
                                                    }else {
                                                        Toast.fail('请输入正确金额', 0.7);
                                                    }
                                                }).catch(err=>{console.log(err)})
                                              }
                                            ]);
                                }.bind(ii)}>下分</Button></div>
                        </div>
                    ))}
                </div>


                <Pagination
                    disabled={this.state.paginationBtnDisable}
                    onChange={(num)=>{
                        this.setState({
                            currentPage:num,
                            paginationBtnDisable:true
                        });
                        this.getUserListInfo(num*11);
                    }}
                    style={{margin:10 }} total={Math.ceil(this.state.totalCount/11)}
                    current={this.state.currentPage} locale={ {prevText: 'Prev',nextText: 'Next'}}
                />
            </div>);
    }
});


