import {NavBar, Pagination} from 'antd-mobile';
var fetch = require('node-fetch');


export const BankerAccount = React.createClass({
    contextTypes: {
        router: React.PropTypes.object
    },
    getInitialState() {
        return {
            windowWidth: (typeof window !== 'undefined') ? window.innerWidth : undefined,
            windowHeight: (typeof window !== 'undefined') ? window.innerHeight : undefined,
            gainNumSum:"loading",
            totalCount:10,
            lotteryRecordList:[],
            paginationBtnDisable:false,
            currentPage:0
        };
    },
    handleResize(e) {
        this.setState({
            windowWidth: window.innerWidth,
            windowHeight: window.innerHeight
        });
    },
    componentDidMount() {
        window.addEventListener('resize', this.handleResize);
        this.getLotteryRecordListInfo(0);
    },
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    },
    getLotteryRecordListInfo(){
        let that = this;
        let Test_Query = `query  getLotteryRecordListFunc($offset: Int!, $count: Int!) {
                             getLotteryRecordList(offset:$offset,count:$count) {
                                  code
                                  type
                                  content
                                }
                             }`;
        fetch(globalServerIP, {
            method: 'POST',
            body: JSON.stringify(
                {
                    "query": Test_Query,
                    "variables": {
                        "offset": arguments[0],
                        "count": 11
                    }
                }
            ),
            headers: {'Content-Type': 'application/json'}
        })
            .then(function (res) {
                return res.json();
            }).then(function (json) {
            let data = JSON.parse(json.data.getLotteryRecordList.content);
            // totalCount,gainNumSum, lotteryRecordList, pageInfo
            //设置变量
            that.setState({
                totalCount:data.totalCount,
                gainNumSum: data.gainNumSum,
                lotteryRecordList: data.lotteryRecordList,
                paginationBtnDisable: false
            });
        }).catch(err=> {
            console.log(err);
        });
    },
    render() {
        const width = this.state.windowWidth;
        const height = this.state.windowHeight;
        let jj = 1;
        return (
            <div>
                <NavBar leftContent="返回菜单"
                        mode="light"
                        onLeftClick={() => {
                            const location = {
                                pathname: '/menu',
                                state: { fromDashboard: true }
                            };
                            this.context.router.history.push(location);
                        }}
                >庄家赢亏统计</NavBar>
                <div style={{display:'flex',flexDirection: 'column'}}>
                    <div
                        style={{display:'flex',alignItems: 'center',justifyContent: 'center',marginLeft:10,marginRight:10,height:height*0.06,marginTop:5}}>
                        <div style={{color:'white',backgroundColor: 'rgba(255,165,0,0.8)',height:height*0.06,display:'flex',alignItems: 'center',justifyContent: 'center',flex:2,borderRadius:height*0.01}}>总盈亏</div>
                        <div style={{marginLeft:10,color:'white',backgroundColor:this.state.gainNumSum<=0?'rgba(0,128,0,0.6)':'rgba(255,0,0,0.6)',height:height*0.06,display:'flex',alignItems: 'center',justifyContent: 'center',flex:2,borderRadius:height*0.01}}>{this.state.gainNumSum}</div>
                    </div>
                    <div
                        style={{display:'flex',alignItems: 'center',justifyContent: 'center',backgroundColor: '#00BFFF',marginLeft:10,marginRight:10,height:height*0.06,paddingLeft:20,borderRadius:height*0.01,marginTop:5}}>
                        <div style={{marginRight:10,color:'white',flex:2}}>期号</div>
                        <div style={{marginRight:10,color:'white',flex:3}}>开奖时间</div>
                        <div style={{marginRight:10,color:'white',flex:3}}>开奖数字</div>
                        <div style={{marginRight:10,color:'white',flex:2}}>盈亏</div>
                    </div>
                    {this.state.lotteryRecordList.map((ii) => (
                        <div key={jj++}
                             style={{display:'flex',alignItems: 'center',justifyContent: 'center',backgroundColor: 'white',marginTop:5,marginLeft:10,marginRight:10,height:height*0.06,paddingLeft:20,borderRadius:height*0.01}}>
                            <div style={{marginRight:10,color:'black',flex:2}}>{ii.periodNum}</div>
                            <div style={{marginRight:10,color:'black',flex:3}}>{ii.lotteryDate}</div>
                            <div style={{marginRight:10,color:'black',flex:3}}>{ii.num1+' + '+ii.num2+' + '+ii.num1+' = '+ii.sum}</div>
                            <div style={{marginRight:10,color:(ii.bettingNum-ii.gainNum)<=0?'rgba(0,128,0,0.6)':'rgba(255,0,0,0.6)',flex:2}}>{ii.bettingNum-ii.gainNum}</div>
                        </div>
                    ))}
                </div>


                <Pagination
                    disabled={this.state.paginationBtnDisable}
                    onChange={(num)=>{
                        this.setState({
                            currentPage:num,
                            paginationBtnDisable:true
                        });
                        this.getLotteryRecordListInfo(num*11);
                    }}
                    style={{margin:10 }} total={Math.ceil(this.state.totalCount/11)}
                    current={this.state.currentPage} locale={ {prevText: 'Prev',nextText: 'Next'}}
                />
            </div>);
    }
});


